import React, { FC } from 'react';
import { useForm } from 'react-hook-form';
import { useSelector } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import { Col, CustomInput, Row } from 'reactstrap';
import { WindowComponent, WindowComponentProps } from 'summer';
import { hasEmpresa } from '../../../../../dependencies/empresaDependency';
import { getDefaultReportBasicValues } from '../../../../../models/dtos/ger/reportBasicValues';
import { getDefaultReportViewOptions, ReportViewOptions } from '../../../../../models/dtos/ger/reportViewOptions';
import { Reducers } from '../../../../../store/ducks';
import { GlobalState } from '../../../../../store/ducks/global.duck';
import EmpresaIS from '../../../../components/ger/empresaIS/EmpresaIS';
import ReportForm from '../../../../components/report/ReportForm';
import { CLASS_KEY, REPORT_EVENT, REPORT_TITLE, SocioParticipacaoRV } from './socioParticipacaoRV';

const DEFAULT_VALUES: SocioParticipacaoRV = {
  ...getDefaultReportBasicValues(REPORT_TITLE),
  empresasSelecionadas: [],
  gerarCabecalhoPadraoAdministradora: true,
};

const DEFAULT_OPTIONS: ReportViewOptions = {
  ...getDefaultReportViewOptions(REPORT_EVENT),
  exibirConfiguracaoDadosEmpresa: false,
};

const SocioParticipacaoReportView: FC<WindowComponentProps & RouteComponentProps> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);

  const { handleSubmit, ...formProps } = useForm<SocioParticipacaoRV>({ defaultValues: DEFAULT_VALUES, mode: 'onBlur' });

  return (
    <WindowComponent
      title="Sócios e Participações"
      useGlobalParameter={true}
      globalParameter={globalReducer.globalParameter}
      dependencies={[() => hasEmpresa(globalReducer.globalParameter)]}
      wikiId={2969}
    >
      <ReportForm
        {...props}
        handleSubmit={handleSubmit}
        formProps={formProps}
        localStorageKey={CLASS_KEY}
        routeProps={props}
        reportBasicValues={DEFAULT_VALUES}
        reportViewOptions={DEFAULT_OPTIONS}
      >
        <Row className="align-items-center">
          <Col xs={1}>
            <CustomInput id="gerarPorEmpresas" innerRef={formProps.register} label="Gerar por Empresas" name="gerarPorEmpresas" type="switch" />
          </Col>
          <Col>
            <EmpresaIS
              disabled={!formProps.getValues().gerarPorEmpresas}
              formProps={{ ...formProps, validation: { required: true } }}
              id="empresasSelecionadas"
              inputSearchProps={{ size: 'xl', selectProps: { isMulti: true } }}
              label="Empresa (s)"
              placeholder="Selecione uma ou várias empresas"
            />
          </Col>
        </Row>
      </ReportForm>
    </WindowComponent>
  );
};

export default SocioParticipacaoReportView;
